import React from 'react'
import '../css/Ticket.css'
import ticketImg from '../assets/ticketImg.png'

function Ticket() {
    return (
        <div className='bg-white'>
            <div className="container" style={{ paddingTop: '9rem' }}>

                <h1 id='ask'><span style={{ color: '#555BF4' }}>EXCITED!?</span> BOOK <br /> YOUR TICKET NOW</h1>

                <div className="ticketImg">
                    <img src={ticketImg} alt="" />
                    <div className="included">
                        <h2>WHAT'S INCLUDED</h2>
                        <ul>
                            <li>Feature 1</li>
                            <li>Feature 2</li>
                            <li>Feature 3</li>
                            <li>Feature 4</li>
                            <li>Feature 5</li>
                        </ul>
                    </div>
                </div>

                <h1 style={{ fontSize: '4rem' }}>CHOOSE YOUR <span style={{ color: '#8154FF' }}>TICKET</span></h1>

                <div className="tickets">
                    <div className="ticketBox">
                        <div className="card" style={{ width: 'fit-content', background: 'linear-gradient(to right, blue, red)' }}>
                            <div className="inner-border">
                                <h1 style={{ color: 'white' }}>BASIC</h1>
                                <p style={{ color: 'white' }}>★★★</p>
                            </div>
                        </div>
                    </div>
                    <div className="ticketBox">
                        <div className="card" style={{ width: 'fit-content', background: 'linear-gradient(to top, black, rgb(167 167 167))' }}>
                            <div className="inner-border">
                                <h1 style={{ color: 'white' }}>SILVER</h1>
                                <p style={{ color: 'white' }}>★★★★</p>
                            </div>
                        </div>
                    </div>
                    <div className="ticketBox">
                        <div className="card" style={{ width: 'fit-content' }}>
                            <div className="inner-border">
                                <h1 style={{ color: 'gold' }}>GOLD</h1>
                                <p style={{ color: 'gold' }}>★★★★★</p>
                            </div>
                        </div>
                    </div>
                </div>
                <center>
                    <button className='btn'>PURCHASE</button>
                </center>
            </div>
        </div>
    )
}

export default Ticket


