import React from 'react'
import Logo from '../assets/Logo.png'
import '../css/Navbar.css'
// import { Storefront, HandWaving, CalendarBlank } from "@phosphor-icons/react";
import store from '../assets/Store.svg'
import calendar from '../assets/Calendar.svg'
import hand from '../assets/Hand.svg'


function Navbar() {
    return (
        <div>
            <nav className='navbar'>
                {/* <div className="container"> */}
                    <div className="logo">
                        <img className='logo-img' src={Logo} alt="logo" />
                        <div className="logo-name">
                            <p>CRAETORS' CAMPUS</p>
                            <p>CONNECT</p>
                        </div>
                    </div>

                    <div className="info">
                        <div className="event">
                        {/* <Storefront size={32} color="#f7f7f7" weight="bold" /> */}
                        <img src={store} alt="" />
                            <p>EVENT</p>
                        </div>
                        <div className="speaker">
                        {/* <HandWaving size={32} color="#f7f7f7" weight="bold" /> */}
                        <img src={hand} alt="" />
                            <p>SPEAKERS</p>
                        </div>
                        <div className="schedule">
                        {/* <CalendarBlank size={32} color="#f7f7f7" weight="bold" /> */}
                        <img src={calendar} alt="" />
                            <p>SCHEDULE</p>
                        </div>
                    </div>
                {/* </div> */}
            </nav>
        </div>
    )
}

export default Navbar


