import React from 'react'
import '../css/Gallery.css'
import Image from '../assets/ajeyNagar2.jpg'
import Img1 from '../assets/img1.jpg'
import Img2 from '../assets/img2.jpg'
import Line from './Line'

function Gallery() {
    return (
        <div className='bg-black' style={{marginTop:'10rem'}}>
            <div className="first">
                <div>
                <h1><span style={{ background: '#8154FF', borderRadius: '60px', padding: '5px 25px', textAlign: 'center' }}>CATCH</span> THE GLIMPSES OF THE <span style={{ color: '#EFD140' }}>EVENT</span></h1>
                <Line/>
                </div>

                <img src={Image} alt="" />
            </div>

            <div className="second">
                <div className="card" style={{ width: '50%', height: '250px', padding: '12px' }}>
                    <div className="inner-border" style={{ height: '100%' }}>

                        <p id='album'>VIEW ALBUMS</p>
                    </div>
                </div>
                <div className="img">
                    <img src={Img1} alt="" />
                    <img src={Img2} alt="" />
                </div>
            </div>
        </div>
    )
}

export default Gallery


